import { Component, OnInit, ViewChild } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { AppConstant } from './app.constant';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  statewiseData: any;
  statewiseColumnDefs = AppConstant.statewiseColumnDefs;
  distrcitWisedata : any;
  distwiseColumnDefs = AppConstant.distwiseColumnDefs;
  dayWiseData : any;
  totalConfirmed: any;
  totalRecoverd: any;
  totalDeaths: any;
  totalTests: any;
  totalActive : any;
  updatedTime : any;
  selectedValue : any;
  statewiseConfirmed : any;
  statewiseActive : any;
  statewiseDeaths : any;
  statewiseRecovered : any;
  statedisplayTable : any = true;
  statedisplayVal : any = true;
  selectedValueDist : string = '';
  distNames : any = [];
  distData : any = [];
  distWisedata : any = [];
  distColumnDefs = AppConstant.distColumnDefs;
  distdisplayTable = false;
  distdisplayVal : any = false;
  distwiseConfirmed : any;
  distwiseActive : any;
  distwiseDeaths : any;
  distwiseRecovered : any;
  /** chart data begins here */
  // chart 1
  dayWiseCharttitle = 'Day wise cases';
  type = 'ColumnChart';
  dayWiseChartData = [];
  dayWiseColumnNames = ['Date', 'Count'];
  options = {
    'legend':'none',
    'width':300
  };

  //chart 2
  dayWiseDeathsCharttitle = 'Day wise Deaths';
  dayWiseDeathChartData = [];

  //chart 3
  dayWiseRecoverdCharttitle = 'Day wise Recoveries';
  dayWiseRecoverdChartData = [];

  //chart 4
  dayWiseTestedCharttitle = 'Day wise Tests';
  dayWiseTestedChartData = [];

  /**  ends here */

  constructor(private http: HttpClient) { }

  ngOnInit() {
    this.getData('https://api.covid19india.org/data.json').subscribe(data => {
      this.dayWiseData = data.cases_time_series;
      this.statewiseData = data.statewise;
      
      this.totalConfirmed = this.statewiseData[0].confirmed;
      this.totalDeaths = this.statewiseData[0].deaths;
      this.totalRecoverd = this.statewiseData[0].recovered;
      this.totalActive = this.statewiseData[0].active;
      this.updatedTime = this.statewiseData[0].lastupdatedtime;
      this.totalTests = data.tested[data.tested.length-1].totalsamplestested;

      this.selectedValue = this.statewiseData[0];
      this.statechange(this.selectedValue);
      let prev = false;

      for(let i =0; i <this.dayWiseData.length;i++)
      {
        let array = [];
        array.push(this.dayWiseData[i].date.trim(),+this.dayWiseData[i].dailyconfirmed);
        this.dayWiseChartData.push(array);
        array = [];
        array.push(this.dayWiseData[i].date.trim(),+this.dayWiseData[i].dailydeceased);
        this.dayWiseDeathChartData.push(array);
        array = [];
        array.push(this.dayWiseData[i].date.trim(),+this.dayWiseData[i].dailyrecovered);
        this.dayWiseRecoverdChartData.push(array);
      }

      for(let j =0; j < data.tested.length;j++)
      {
        let array = [];
        if( j != 0)
        {
          if(data.tested[j].totalsamplestested == ""){
            array.push(data.tested[j].updatetimestamp.split(' ')[0],0);
            prev = true;
          } 
          else{
            if(prev)
            {
              array.push(data.tested[j].updatetimestamp.split(' ')[0],+data.tested[j].totalsamplestested);
             }
            else{
              array.push(data.tested[j].updatetimestamp.split(' ')[0],(+data.tested[j].totalsamplestested) - (+data.tested[j-1].totalsamplestested));            
            prev = false;
            }
            
          }          
        }        
        else
        {
          array.push(data.tested[j].updatetimestamp.split(' ')[0],+data.tested[j].totalsamplestested);
        }
        this.dayWiseTestedChartData.push(array);       
      }
    });

  }

  getData(url): Observable<any> {
    return this.http.get(url);
  }

  statechange(stateItem)
  {
    this.statewiseConfirmed = stateItem.confirmed;
    this.statewiseActive = stateItem.active;
    this.statewiseDeaths = stateItem.deaths;
    this.statewiseRecovered = stateItem.recovered;
    this.statedisplayTable = false;
    this.distdisplayTable = false;
    this.statedisplayVal = true;
    this.distdisplayVal = false;
    if(stateItem.state != 'Total')
    {
      this.statedisplayTable = true; 
      this.statedisplayVal = true;
      this.getData('https://api.covid19india.org/state_district_wise.json').subscribe(data => {
      const usersJson: any[] = Array.of(data[stateItem.state].districtData);

      let arr = [];
      Object.keys(usersJson[0]).map(function (key) {
        let userdata = usersJson[0][key];
        userdata['disctrictName'] = key;
        arr.push(userdata);
        return arr;
      });      
      this.distrcitWisedata = arr;
    });
    this.getData('https://api.covid19india.org/districts_daily.json').subscribe(data => {
      this.distNames = [];
      this.distWisedata = data.districtsDaily[stateItem.state];
      this.distNames.push('Select');
      let distArr = Object.keys(this.distWisedata);
      for(let i = 0; i<distArr.length;i++)
      {
        this.distNames.push(distArr[i]);
      }      
    });
    
    }
    else
    {
      this.statedisplayTable = false;
      this.distdisplayTable = false;
    }   
    
  }

  distChange(item)
  {
    if(item != 'Select')
    {
      this.statedisplayTable = false;
      this.statedisplayVal = false;
      this.distdisplayTable = true;
      this.distdisplayVal = true;
      this.distData = this.distWisedata[item];
      this.distwiseActive = this.distData[this.distData.length-1].active;
      this.distwiseConfirmed = this.distData[this.distData.length-1].confirmed;
      this.distwiseDeaths = this.distData[this.distData.length-1].deceased;
      this.distwiseRecovered = this.distData[this.distData.length-1].recovered;
    }
    else{
      this.statedisplayTable = true;
      this.distdisplayTable = false;
      this.statedisplayVal = true;
      this.distdisplayVal = false;
    }    
  }
}
